.homeSection {
  min-height: 100vh;
  min-width: 100vw;
  /*overflow: hidden;*/

}

.particles {
  background-image: linear-gradient(to top right, #3629a7, #343a40);
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}

.intro {

  font-size: 32pt;
  line-height: 36pt;
  text-align: center;
  width: fit-content;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

}

.line {
  width: 780px;
  background-color: #BBD336;
  height: 3px;
  padding: 0;
}


.heroText {

  :nth-child(2) {
    color: #ea2c62;
  }

  :nth-child(4) {
    height: 100px;
    color: #d37815;
  }

  //border: 1px solid;
  position: absolute;
  top: 0%;
  left: calc((100% - 780px) / 2);
  z-index: 300;

  h1 {
    margin: 10px 0 20px 0;
    letter-spacing: -1px;
    font-family: Playfair Display, serif;
    font-weight: 700;
    font-size: 55px;
    line-height: 1;
    text-align: center;
    color: #fff;
  }
}

.section {
  height: 100vh;
  width: 100vw;
  position: relative;
  z-index: 2;


  .whiteMask {
    bottom: 0;
    position: absolute;
    z-index: 1;
    left: 50%;
    min-width: 100%;
    height: 400px;
    max-height: 40%;
    display: block;
    transform: translateX(-50%);
  }
}


@media screen and (max-width: 859px) {
  .heroText {
    left: calc((100% - 560px) / 2);

    >h1 {
      font-size: 40px;
    }
  }
  .line {
    width: 560px;
  }
}


@media screen and (max-width: 560px) {
  .heroText {
    left: calc((100% - 415px) / 2);

    >h1 {
      font-size: 30px;
    }
  }
  .line {
    width: 415px;
  }
  .welcomeTo {
    width: 80%;
  }
}

@media screen and (max-width: 420px) {
  .heroText {
    left: calc((100% - 270px) / 2);

    >h1 {
      font-size: 20px;
    }
  }
  .line {
    width: 270px;
  }
}

@media screen and (max-width: 275px) {
  .heroText {
    left: calc((100% - 212px) / 2);

    >h1 {
      font-size: 16px;
    }
  }
  .line {
    width: 212px;
  }
}

.highlight {
  color: #e31b6d;
  font-family: "Raleway-semibold";
}